
import XHeader from 'vux/src/components/x-header/index.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import { mapState, mapGetters } from "vuex";

const list = () => ["季度", "年度"];
export default {
components: {
XHeader,
Sticky,
Tab,
TabItem
},
computed: {
...mapGetters(["integPopup"])
},
data() {
return {
list: list(),
index: 0,
selected: "",
disabled:
typeof navigator !== "undefined" &&
/iphone/i.test(navigator.userAgent) &&
/ucbrowser/i.test(navigator.userAgent)
};
},
watch: {
$route(to, from) {
if (to.query.status == "quarter") {
this.selected = "季度";
}
}
},
created() {
let currentSelected = this.$route.query.status;
if (currentSelected === "quarter") {
this.selected = "季度";
} else if (currentSelected === "year") {
this.selected = "年度";
}
},
methods: {
handler(item) {
this.selected = item;
let path = this.$route.path;
if (item === "季度") {
this.$router.replace(`${path}?status=quarter`);
} else if (item === "年度") {
this.$router.replace(`${path}?status=year`);
}
},
changChoose() {
this.$store.dispatch("updateIntegPopup", {
integPopup: true
});
}
}
};



